import * as React from 'react';
import { Icon24Cancel, Icon24Chevron, Icon24Dismiss, Icon24DismissDark } from '@vkontakte/icons';
import { classNames, hasReactNode } from '@vkontakte/vkjs';
import { usePlatform } from '../../hooks/usePlatform';
import { Platform } from '../../lib/platform';
import { HTMLAttributesWithRootRef } from '../../types';
import { IconButton } from '../IconButton/IconButton';
import { RootComponent } from '../RootComponent/RootComponent';
import { Tappable } from '../Tappable/Tappable';
import { Headline } from '../Typography/Headline/Headline';
import { Subhead } from '../Typography/Subhead/Subhead';
import { Text } from '../Typography/Text/Text';
import { Title } from '../Typography/Title/Title';
import styles from './Banner.module.css';

export interface BannerProps extends HTMLAttributesWithRootRef<HTMLDivElement> {
  /**
   *  .
   */
  mode?: 'tint' | 'image';
  size?: 's' | 'm';
  /**
   *      .
   *
   * - `dismiss`    ,       `onDismiss`.
   * - `expand`    ,  ,       - .
   */
  asideMode?: 'dismiss' | 'expand';
  /**
   *        `asideMode="dismiss"`.
   */
  onDismiss?: React.MouseEventHandler<HTMLButtonElement>;
  /**
   * `aria-label`    `asideMode="dismiss". ,    .
   */
  dismissLabel?: string;
  /**
   * ,     .
   */
  before?: React.ReactNode;
  /**
   * .
   */
  header?: React.ReactNode;
  /**
   * .
   */
  subheader?: React.ReactNode;
  /**
   *  .
   */
  text?: React.ReactNode;
  /**
   *   `mode="image"`.
   *
   * - `light`       ,      .
   * - `dark`       ,    .
   */
  imageTheme?: 'light' | 'dark';
  /**
   *   `mode="image"`.
   *
   * ,     / .      100%    .
   */
  background?: React.ReactNode;
  /**
   * -.  [`Button`](https://vkcom.github.io/VKUI/#/Button).
   *
   * -   `tint`  `image`       :
   *    - `mode="primary"`
   *    - `mode="secondary"`
   * -   `image`      :
   *    - `appearance="overlay"`.
   *
   *     [`ButtonGroup`](https://vkcom.github.io/VKUI/#/ButtonGroup)  :
   *
   * - `gap="m" mode="horizontal" stretched`
   * - `gap="m" mode="vertical" stretched`
   */
  actions?: React.ReactNode;
  /**
   *    
   */
  noPadding?: boolean;
}

/**
 * @see https://vkcom.github.io/VKUI/#/Banner
 */
export const Banner = ({
  mode = 'tint',
  imageTheme = 'dark',
  size = 's',
  before,
  asideMode,
  header,
  subheader,
  text,
  children,
  background,
  actions,
  onDismiss,
  dismissLabel = '',
  noPadding,
  ...restProps
}: BannerProps) => {
  const platform = usePlatform();

  const HeaderTypography = size === 'm' ? Title : Headline;
  const SubheaderTypography = size === 'm' ? Text : Subhead;

  const IconDismissIOS = mode === 'image' ? Icon24DismissDark : Icon24Dismiss;

  const content = (
    <>
      {mode === 'image' && background && (
        <div aria-hidden className={styles['Banner__bg']}>
          {background}
        </div>
      )}

      {before && <div className={styles['Banner__before']}>{before}</div>}

      <div className={styles['Banner__content']}>
        {hasReactNode(header) && (
          <HeaderTypography Component="p" weight="2" level={size === 'm' ? '2' : '1'}>
            {header}
          </HeaderTypography>
        )}
        {hasReactNode(subheader) && (
          <SubheaderTypography Component="p" className={styles['Banner__subheader']}>
            {subheader}
          </SubheaderTypography>
        )}
        {hasReactNode(text) && (
          <Text Component="p" className={styles['Banner__text']}>
            {text}
          </Text>
        )}
        {hasReactNode(actions) && React.Children.count(actions) > 0 && (
          <div className={styles['Banner__actions']}>{actions}</div>
        )}
      </div>
    </>
  );

  return (
    <RootComponent
      Component="section"
      {...restProps}
      baseClassName={classNames(
        styles['Banner'],
        !noPadding && styles['Banner--withPadding'],
        platform === Platform.IOS && styles['Banner--ios'],
        mode === 'image' && styles['Banner--mode-image'],
        size === 'm' && styles['Banner--size-m'],
        mode === 'image' && imageTheme === 'dark' && styles['Banner--inverted'],
      )}
    >
      {asideMode === 'expand' ? (
        <Tappable
          className={styles['Banner__in']}
          activeMode={platform === Platform.IOS ? 'opacity' : 'background'}
          role="button"
        >
          {content}

          <div className={styles['Banner__aside']}>
            <Icon24Chevron className={styles['Banner__expand']} />
          </div>
        </Tappable>
      ) : (
        <div className={styles['Banner__in']}>
          {content}

          {asideMode === 'dismiss' && (
            <div className={styles['Banner__aside']}>
              <IconButton
                aria-label={dismissLabel}
                className={styles['Banner__dismiss']}
                onClick={onDismiss}
                hoverMode="opacity"
                hasActive={false}
              >
                {platform === Platform.IOS ? <IconDismissIOS /> : <Icon24Cancel />}
              </IconButton>
            </div>
          )}
        </div>
      )}
    </RootComponent>
  );
};
